.kanban-container{margin-top:0px;position:relative;box-sizing:border-box;width:auto}
.kanban-container *{box-sizing:border-box}
.kanban-container:after{clear:both;display:block;content:""}
.kanban-board{position:relative;float:left;background:transparent;transition:all .3s cubic-bezier(.23,1,.32,1)}
.kanban-board.disabled-board{opacity:.3}.kanban-board.is-moving.gu-mirror{transform:rotate(3deg)}
.kanban-board.is-moving.gu-mirror .kanban-drag{overflow:hidden;padding-right:50px}
.kanban-board header{
    border-bottom: 5px solid #e2e4e6;
    font-family:'Roboto';
    font-weight:lighter;
    font-size:20px;
    padding:15px
}


.kanban-board header .kanban-title-board{font-weight:700;margin:-10px;padding:0;display:inline}
.kanban-board header .kanban-title-button{float:right}.kanban-board .kanban-drag{min-height:200px;padding:20px}
.kanban-board:after{clear:both;display:block;content:""}
.kanban-item{font-family:'Raleway', sans-serif;background:#e2e4e6;padding:15px;margin-bottom:20px;transition:all .3s cubic-bezier(.23,1,.32,1);animation:append-animate .3s cubic-bezier(.23,1,.32,1)}@keyframes append-animate{from{transform:translateY(-20px)}to{transform:translateY(0)}}
.kanban-item:hover{cursor:move}
.kanban-item:last-child{margin:0}
.kanban-item.is-moving.gu-mirror{transform:rotate(3deg);height:auto!important}.gu-mirror{position:fixed!important;margin:0!important;z-index:9999!important}
.gu-hide{display:none!important}
.gu-unselectable{-webkit-user-select:none!important;-moz-user-select:none!important;-ms-user-select:none!important;user-select:none!important}
.gu-transit{opacity:.2!important;transform:rotate(0)!important}
.drag_handler{background:#fff;border-radius:50%;width:24px;height:24px;position:relative;float:left;top:-3px;margin-right:4px}.drag_handler:hover{cursor:move}
.drag_handler_icon{position:relative;display:block;background:#000;width:24px;height:2px;top:12px;transition:.5s ease-in-out}
.drag_handler_icon:after,.drag_handler_icon:before{background:#000;content:'';display:block;width:100%;height:100%;position:absolute;transition:.5s ease-in-out}
.drag_handler_icon:before{top:6px}
.drag_handler_icon:after{bottom:6px}

#removeButton{
    position: absolute;
    top: 85%;
    left: 80%;
    width: 150px;
    height: 40px;
    margin-left: 20px;
    font-family: 'Raleway', sans-serif;
    border: none;
    background-color: transparent;
    color: #034EFC;
    font-weight: bold;
    cursor: pointer;
}

#addDefault{
    top: 80%;
    left: 80%;
    position: absolute;
    width: 150px;
    height: 40px;
    margin-left: 20px;
    font-family: 'Raleway', sans-serif;
    border: none;
    background-color: transparent;
    color: #034EFC;
    font-weight: bold;
    cursor: pointer;
}

#addDefault:hover{
    background-color: #034EFC;
    color: white;
}

#removeButton:hover{
    background-color: #034EFC;
    color: white;
}

.kanban-board header:hover{
    cursor: move;
}

.kanban-title-board{
    cursor: text;
}